<template>
  <x-crud :loading="loading">
    <x-form v-model="formData" :values="formData" @submit="onsubmit">
      <a-row :gutter="[24, 24]">
        <a-col
          :md="{ span: 6, offset: 9 }"
          :lg="{ span: 6, offset: 9 }"
          :xl="{ span: 6, offset: 9 }"
          class="col-form"
        >
          <x-input
            label="Название"
            name="name"
            type="text"
            validation="required"
            :disabled="disabled"
          ></x-input>
          <div class="crud-form-button">
            <a-button type="primary" block html-type="submit">
              {{ getButtonAction }}
            </a-button>
          </div>
        </a-col>
      </a-row>
    </x-form>
  </x-crud>
</template>

<script>
import crudMixin from '@/mixins/crudMixin'
import store from '@/store'
export default {
  props: ['action', 'id'],
  mixins: [crudMixin],
  data() {
    return {
      formData: {},
      nomination: null,
      entityName: 'nomination',
      textareaMaxLength: 255,
    }
  },
  methods: {
    async onsubmit() {
      if (this.action === 'read') {
        this.$router.push(`/nomination/edit/${this.id}`)
        return
      }
      await this.saveEntity(this.formData)
    },
  },
  async created() {
    if (this.action !== 'create') await this.getEntity(this.id)
    this.loading = false
    console.log(this.action, this.id, this.disabled)
  },
}
</script>

